import React, { useState, useEffect } from 'react';

function current_date() {
  const today = new Date();
  const timeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
  const localDate = today.toLocaleDateString('en-CA', { timeZone: timeZone });
  return localDate;

}

function TimeBlockForm({ onSubmit , onUpdateDate  }) {
  const [date, setDate] = useState(''); //   
  const [startTime, setStartTime] = useState('');
  const [endTime, setEndTime] = useState('');
  const [task, setTask] = useState('');

  
  useEffect(() => {
    //   
    const localDate = current_date()
    setDate(localDate); // 'YYYY-MM-DD'    
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    onSubmit([{ date, startTime, endTime, task }]);
    setStartTime('');
    setEndTime('');
    setTask('');
  };

  const handleWorkdaySetting = () => {
    const confirmSave = window.confirm('  ?');
    if (confirmSave) {
      //     
      const presets = [
        { date:date, startTime: '09:00', endTime: '18:00', task: '' },
        { date:date, startTime: '11:30', endTime: '13:00', task: ' ' },
      ];
      //       
      onSubmit(presets);
      //   
      alert('  .');
    }
  };
  
  const handleStartTimeChange = (e) => {
    const newStartTime = e.target.value;
    setStartTime(newStartTime);
    if (!endTime) { //    ,   
      setEndTime(newStartTime);
    }
  };

  const handleEndTimeChange = (e) => {
    const newEndTime = e.target.value;
    setEndTime(newEndTime);
    if (!startTime) { //    ,   
      setStartTime(newEndTime);
    }
  };
  //       
  const handleTodaySetting = () => {
    const today = current_date();
    onUpdateDate(today); //     

  }
  return (
    <form onSubmit={handleSubmit} className="time-block-form">
      <div className="form-group">
        <label htmlFor="date">:</label>
        <input
          type="date"
          id="date"
          value={date}
          onChange={(e) => setDate(e.target.value)}
          required
        />
      </div>
      <div className="form-group">
        <label htmlFor="startTime"> :</label>
        <input
          type="time"
          id="startTime"
          value={startTime}
          onChange={handleStartTimeChange}
          required
        />
      </div>
      <div className="form-group">
        <label htmlFor="endTime"> :</label>
        <input
          type="time"
          id="endTime"
          value={endTime}
          onChange={handleEndTimeChange}
          required
        />
      </div>
      <div className="form-group">
        <label htmlFor="task"> :</label>
        <input
          type="text"
          id="task"
          value={task}
          onChange={(e) => setTask(e.target.value)}
          placeholder="  "
          required
        />
      </div>
      <button type="submit" className="submit-btn"></button>
      <button type="button" onClick={handleWorkdaySetting} className="preset-btn"> </button>
      <button 
      type="button" 
      className='preset-btn' 
      onClick={handleTodaySetting} 
      >   </button>
    </form>
  );
}

export default TimeBlockForm;
